<template>
  <div>
    <el-dialog
      title="详情"
      :visible="detail"
      :modal-append-to-body="false"
      :before-close="onClose"
    >
      <el-form :model="form" :rules="rules" ref="form" class="form_box">
        <el-form-item label="设备名称" prop="DeviceName">
          <el-input
            v-model="form.DeviceName"
            class="input_box"
            disabled
          ></el-input>
        </el-form-item>
        <el-form-item label="设备类型" prop="DeviceType">
          <el-input
            v-model="form.DeviceType"
            class="input_box"
            disabled
          ></el-input>
        </el-form-item>
        <el-form-item label="设备编号" prop="DeviceNumber">
          <el-input
            v-model="form.DeviceNumber"
            class="input_box"
            disabled
          ></el-input>
        </el-form-item>
        <el-form-item label="资产编号" prop="AssetNumber">
          <el-input
            v-model="form.AssetNumber"
            class="input_box"
            disabled
          ></el-input>
        </el-form-item>
        <el-form-item label="维修开始时间" prop="StartTime">
          <el-date-picker
            v-model="form.StartTime"
            type="datetime"
            placeholder="选择日期"
            value-format="yyyy-MM-dd HH:mm:ss"
            disabled
          >
          </el-date-picker>
        </el-form-item>
        <el-form-item label="维修结束时间" prop="EndTime">
          <el-date-picker
            v-model="form.EndTime"
            type="datetime"
            placeholder="选择日期"
            value-format="yyyy-MM-dd HH:mm:ss"
            disabled
          >
          </el-date-picker>
        </el-form-item>
        <el-form-item label="维修原因" prop="reason">
          <el-input
            type="textarea"
            row="2"
            v-model="form.reason"
            class="input_box"
            disabled
          ></el-input>
        </el-form-item>
        <el-form-item label="维修人员" prop="RepairPersonnel">
          <el-input
            v-model="form.RepairPersonnel"
            class="input_box"
            disabled
          ></el-input>
        </el-form-item>
        <el-form-item label="维修状态" prop="RepairState">
          <el-select v-model="form.RepairState" disabled>
            <el-option
              v-for="item in RepairStateData"
              :key="item.id"
              :label="item.label"
              :value="item.value"
              disabled
            ></el-option>
          </el-select>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>
<script>
export default {
  props: {
    detail: {
      type: Boolean,
      require: true,
    },
    data: {
      type: Object,
      require: true,
    },
  },
  data() {
    return {
      form: {
        DeviceName: "",
        DeviceType: "",
        DeviceNumber: "",
        AssetNumber: "",
        StartTime: "",
        EndTime: "",
        RepairPersonnel: "",
        reason: "",
        RepairState: "",
      },
      RepairStateData: [
        {
          id: 1,
          value: 1,
          label: "完成",
        },
        {
          id: 0,
          value: 0,
          label: "未完成",
        },
      ],
      rules: {
        DeviceName: [
          { required: true, message: "请输入设备名称", trigger: "blur" },
        ],
        DeviceType: [
          { required: true, message: "请输入设备类型", trigger: "blur" },
        ],
        DeviceNumber: [
          { required: true, message: "请输入设备编号", trigger: "blur" },
        ],
        AssetNumber: [
          { required: true, message: "请输入资产编号", trigger: "blur" },
        ],
        StartTime: [
          { required: true, message: "请选择开始时间", trigger: "blur" },
        ],
        EndTime: [
          { required: true, message: "请选择结束时间", trigger: "blur" },
        ],
        RepairPersonnel: [
          { required: true, message: "请输入维修人员", trigger: "blur" },
        ],
        reason: [
          { required: true, message: "请输入维修原因", trigger: "blur" },
        ],
        RepairState: [
          {
            required: true,
            message: "请选择维修状态",
            trigger: "blur",
          },
        ],
      },
    };
  },
  created() {
    this.form = this.data;
  },
  watch: {
    data() {
      this.form = this.data;
    },
  },
  methods: {
    onClose() {
      this.$emit("update:detail", false);
    },
  },
};
</script>
<style lang="scss" scoped>
.form_box {
  padding: 0 230px;
}
/deep/.el-form-item__label {
  font-size: 18px;
}
.input_box {
  width: 400px;
}
.btns {
  text-align: center;
}
.input_box {
  width: 400px;
}
</style>
